/* harmony default export */ __webpack_exports__["default"] = ("<div class=\"container\" style=\"margin-top: 100px;\">\n    <div class=\"text-center mt-4 mb-4\">\n        <h1 style=\"color: crimson;\">History and background</h1>\n    </div>\n    <div>\n        <p class=\"lead\" style=\"text-align: justify;\">\n            Christian Aid an International Resource Agency completed its 5o years of service in the year 2000 in India.\n            In the memory of this, Christian aid wanted to launch an independent program for women. The then Asia\n            Coordinator Mr. Tom Palakudy approached Social centre which is a centre for social apostolate for Pune\n            Jesuits, to design a program. The basic idea of Christian aid was to initiate a resource centre for women’s\n            empowerment which would be for women and managed by a group of women.\n        </p>\n        <p class=\"lead\" style=\"text-align: justify;\">\n            The program was initiated in the year 2000 at social centre with Br. Noel as a coordinator till a women\n            co-ordinator was found . To fulfill the commitment of the funding agency of having a woman head , Pune\n            Jesuits in the year 2002 invited a religious congregation involved with women’s development programme namely\n            ‘Society of Sacred Heart’ to implement the program and the administration was shifted to Bhokar from where\n            the program is presently facilitated. The program was registered as Ashankur Charitable Trust in Aug 2004\n            under the Societies Act as a collaborative venture between Pune Jesuits and Society of Sacred Heart with its\n            aim of empowering women.\n\n\n        </p>\n        <p class=\"lead\" style=\"text-align: justify;\">\n            Today it has its net work with 5000 grass root women in the district. It works with labourer and farmer\n            women by organizing them and training them for livelihood and social skills . It conducts different\n            vocational training for girls who are school drop outs and struggling women of rural India .\n        </p>\n    </div>\n</div>\n<div class=\"container mt-5 mb-5\">\n    <div class=\"row mt-5\">\n        <div class=\"col-sm-12\">\n            <img class=\"img-fluid shadow\" src=\"assets/images/aboutHistory.jpg\" alt=\"\" style=\"height: 400px;width: 1200px;\">\n        </div>\n    </div>\n</div>");

