/* harmony default export */ __webpack_exports__["default"] = ("<div class=\"row mt-5\">\n    <div class=\"col-sm-12 col-lg-10 offset-lg-1\">\n        <div class=\"text-center mt-4 mb-4\">\n            <h1 style=\"color: crimson;\">Events</h1>\n        </div>\n    </div>\n    <div class=\"col-sm-12 col-lg-10 offset-lg-1\">\n        <h5>1) Women’s Day </h5>\n        <p  class=\"lead\" style=\"text-align: justify;\">\n            On 16th March 2019,   was a great day for all belonging to Ashankur family as it celebrated the most awaited event of the year – women’s day.  The theme for the celebration was “ changlya arogyachi ahe aas, sendriya annache ghya ghas” which is also connected with the theme for the international womens’ day 2019 “ Balance for better” which also means  Giving way to organic farming and putting an end to the cultivation done with the use of pesticide. About 1000 women from 180 Self Help Groups from 3 talukas came for the celebration to Ashankur Bhokar.Mrs. AnuradhaVandekar  from PIRENS - Bableshwar ( Pravara Institute of Research and Education in Natural and Social sciences ) was the chief guest for the day.  She spoke on the importance of organic farming. The second  Speaker Mrs. Nisha Kishore Nirmal the director of Prabhat Diary also guided the women regarding health and how we knowingly or unknowingly feed our children with poisonous food, instead why not switch on to organic farming in order to save the mother earth, take care of today’s generation and also give way to a healthier living.  \n        </p>\n    </div>\n    <div class=\"col-sm-12 col-lg-10 offset-lg-1 mt-3\">\n        <h5>2) Lamp Lighting Ceremony</h5>\n        <p  class=\"lead\" style=\"text-align: justify;\">On 13th February, 25 nursing students took the oath to serve the patients with love and compassion. There are 12 students who are helped in enrolment for RANM ( Revised Auxiliary Nursing Midwifery) for two years  that is Government course after 12std. They are sent to bhinav Institute of Nursing Education  Miraj,Sangli to continue their studies.    They had to go regularly to do their exams at this institute.And 13 students completed one year course. And now they are working in different clinic ,oldage home and private hospital in Pune. </p>\n    </div>\n    <div class=\"col-sm-12 col-lg-10 offset-lg-1 mt-3 mb-3\">\n        <h5>3) Children’s Day Celebration </h5>\n        <p  class=\"lead\" style=\"text-align: justify;\">\n            On 2nd December 2018 it was a special day for the children of our remedial centres as Ashankur celebrated the 8th children’s day since the project began.  It was a long awaited day for the children as they came to Ashankur, Bhokar to celebrate children’s day.  Sr. Prisca the directress of Ashankur welcomed all present for the day - the guests, the teachers and the children.  The celebration began with the prayer dance and the lighting of the lamp by the chief guests.\n        </p>\n    </div>\n</div>\n");

