/* harmony default export */ __webpack_exports__["default"] = ("<div class=\"container\"  style=\"margin-top: 50px;\">\n    <div class=\"text-center mt-4 mb-4\">\n        <h1 style=\"color: crimson;\">Home Nursing Course</h1>\n    </div>\n    <div>\n        <p class=\"lead\" style=\"text-align: justify;\">\n            Due to financial restraints many young women are not able to avail of the Nursing training run by the\n            Government. Often those who have not completed their schooling are destined to spend the years before an\n            early marriage as daily wage labourers.\n        </p>\n        <p class=\"lead\" style=\"text-align: justify;\">    \n            The Home Nurse’s Aid Course of a year’s duration is available to those who have a minimum education of Std\n            VIII as well as to those who have studied further. Most of the girls get jobs within 2-3 months of the\n            completion of the course. The girls trained in the nursing program work in different dispensaries and\n            hospitals, and they are able to earn a fair salary, while also contributing to the health and welfare of the\n            people in the surrounding region.\n        </p>            \n        <p class=\"lead\" style=\"text-align: justify;\">\n            Those who have completed Std. XII have also been able to complete the RANM Course externally.\n        </p>            \n    </div>\n</div>\n<div class=\"container mt-5 mb-5\">\n    <div class=\"row mt-5\">\n        <div class=\"col-sm-4\">\n            <img class=\"img-fluid\" src=\"assets/images/HomeNursing11.jpg\" alt=\"\">\n        </div>\n        <div class=\"col-sm-4\">\n            <img class=\"img-fluid\" src=\"assets/images/HomeNursing22.jpg\" alt=\"\">\n        </div>\n        <div class=\"col-sm-4\">\n            <img class=\"img-fluid\" src=\"assets/images/HomeNursing3.jpg\" alt=\"\">\n        </div>\n    </div>\n</div>");

