/* harmony default export */ __webpack_exports__["default"] = ("<div class=\"container\"  style=\"margin-top: 50px;\">\n    <div class=\"text-center  mt-4 mb-4\">\n        <h1 style=\"color: crimson;;\">Organic Farming</h1>\n    </div>\n    <div>\n        <p class=\"lead\" style=\"text-align: justify;\">\n            The environmental crisis that is gripping the world is addressed by Ashankur through initiatives in the area\n            of organic farming. The purpose of this intervention is to save the soil from being destroyed by chemical\n            fertilizers as well as to improve the quality of the health of the people.\n        </p>\n        <p class=\"lead\" style=\"text-align: justify;\">\n            Over a period of five years several groups of men and women have been trained to prepare manure and\n            pesticides that are eco-friendly and organic. It is only after seeing the difference in the taste of\n            vegetables grown organically and those grown with the help of pesticides that the women are convinced of the\n            value of this program.\n        </p>\n    </div>\n</div>\n<div class=\"container  mt-5 mb-5\">\n    <div class=\"row\">\n        <div class=\"col-sm-6\">\n            <img class=\"img-fluid\" src=\"assets/images/organicfarm1.jpg\" alt=\"\">\n        </div>\n        <div class=\"col-sm-6\">\n            <img class=\"img-fluid\" src=\"assets/images/organicfarm22.jpg\" alt=\"\">\n        </div>\n    </div>\n</div>");

