/* harmony default export */ __webpack_exports__["default"] = ("<div class=\"container\"  style=\"margin-top: 50px;\">\n    <div class=\"text-center mt-4 mb-4\">\n        <h1 style=\"color: crimson;\">Remedial Classes</h1>\n    </div>\n    <div>\n        <p class=\"lead\" style=\"text-align: justify;\">\n            The facilities that are available to children from the cities are not available to children in the rural\n            areas. Yet these children have to compete on an equal footing with children of their age in colleges and\n            universities. The changing policies in the educational field have left the rural children , whose parents\n            are often uneducated, at a disadvantage.\n        </p>\n        <p class=\"lead\" style=\"text-align: justify;\">\n            Ashankur is running remedial classes in 12 different villages to cater to 300 children in 16 classes. The\n            animators are from the village itself, and can keep in touch with the parents of the children. The staff of\n            Ashankur visit each class at least once a month to monitor the teaching.\n        </p>\n        <p class=\"lead\" style=\"text-align: justify;\">\n            The Centre also conducts regular training programs to empower the animators.\n        </p>\n        <p class=\"lead\" style=\"text-align: justify;\">\n            The high lights of the year are the Quiz competition to enrich their general knowledge, the Children’s Day\n            celebration to give the children the opportunity to exhibit their talents, the elocution competition and the\n            educational trip besides other programs.\n        </p>\n    </div>\n</div>\n<div class=\"container mt-5 mb-5\">\n    <div class=\"row\">\n        <div class=\"col-sm-4\">\n            <img class=\"img-fluid\" src=\"assets/services/remedialClasses1.jpg\" alt=\"\">\n        </div>\n        <div class=\"col-sm-4\">\n            <img class=\"img-fluid\" src=\"assets/services/remedialClasses2.jpg\" alt=\"\" style=\"height: 324px;\">\n        </div>\n        <div class=\"col-sm-4\">\n            <img class=\"img-fluid\" src=\"assets/services/remedialClasses3.jpg\" alt=\"\" style=\"height: 324px; width: 400px;\">\n        </div>\n    </div>\n</div>");

