/* harmony default export */ __webpack_exports__["default"] = ("<div class=\"container\"  style=\"margin-top: 50px;\">\n    <div class=\"text-center mt-4 mb-4\">\n        <h1 style=\"color: crimson;\">Tailoring</h1>\n    </div>\n    <div>\n        <p class=\"lead\" style=\"text-align: justify;\">\n            The tailoring Course with a duration of six months equips young women to have their own tailoring shops. This has benefitted young women from remote villages where the nearest tailor is miles away. They are also able to sew clothes for their own families. \n        </p>\n        <p class=\"lead\" style=\"text-align: justify;\">    \n            Many women who are not able to take on a full time job outside can earn enough by managing their duties at home as well as be earning members of their families.\n        </p>            \n        <p class=\"lead\" style=\"text-align: justify;\">\n            One of the small income generating projects as an off shoot of the tailoring course is taking on a contract to provide school uniforms to the local school.\n        </p>            \n    </div>\n</div>\n<div class=\"container mt-5 mb-5\">\n    <div class=\"row mt-5\">\n        <div class=\"col-sm-6\">\n            <img class=\"img-fluid\" src=\"assets/services/Taining women for income generation.jpg\" style=\"height: 450px;width: 100%;\">\n        </div>\n        <div class=\"col-sm-6\">\n            <img class=\"img-fluid\" src=\"assets/images/HomeNursing22.jpg\" style=\"height: 450px;width: 100%;\">\n        </div>\n    </div>\n</div>");

