/* harmony default export */ __webpack_exports__["default"] = ("\n<div class=\"container-fluid\" style=\"margin-top: 50px;\">\n    <div class=\"card card-body\">\n    <div class=\"row  mt-4 mb-4\">\n        <div class=\"col-sm-9\">\n            <h4 style=\"color: crimson;\"> 1) Kalpana Jaju</h4>\n            <p class=\"lead\" style=\"text-align: justify;\">\n                A widow three times over, Kalpana had to find some means of livelihood for herself and a daughter.\n                Dauntless in her efforts to live a dignified life, she was willing to do menial work in the\n                neighbourhood – sweeping and swabbing , cooking etc. She then took on small sewing jobs and was willing\n                to walk long distances to save even a rupee.\n            </p>\n\n            <p class=\"lead\" style=\"text-align: justify;\">\n                It was when she formed a Self Help Group of 20 women from her neighbourhood that she gained confidence .\n                With the help of a loan from the Revolving Fund she was able to open a small shop to sell saries . This\n                has helped her to be quite self sufficient.\n\n            </p>\n        </div>\n        <div class=\"col-sm-3\">\n            <img class=\"img-fluid\" src=\"assets/images/success-stories1.jpg\" alt=\"\"  style=\"height:350px;width: 100%;\">\n        </div>\n    </div>\n</div>\n\n<div class=\"container-fluid mt-3 mb-3 p-0\">\n    <div class=\"card card-body\">\n    <div class=\"row  mt-4 mb-4\">\n        <div class=\"col-sm-9\">\n            <h4 style=\"color: crimson;\">2) Chandrakala Gaydhane</h4>\n            <p class=\"lead\" style=\"text-align: justify;\">\n                When she joined the Vaishnavi Self Help Group in a small village in the Shrirampur taluka of Ahmednagar\n                District, she had a great desire to bring about some change in the village. She had not even finished\n                high school, but this did not deter her from attempting to become the Police Patil ( Juridical head in a\n                village).\n            </p>\n\n            <p class=\"lead\" style=\"text-align: justify;\">\n                Over the last fifteen years , being the Police Patil, she has voiced the needs of the village – water,\n                food grains, sanitation etc . This has made a great difference to the village. She enjoys the respect\n                and support of her people. The village has been awarded a prize for the progress it has made.\n            </p>\n        </div>\n        <div class=\"col-sm-3\">\n            <img class=\"img-fluid\" src=\"assets/images/success-stories2.jpg\" alt=\"\"  style=\"height:350px;width: 100%;\">\n        </div>\n    </div>\n</div>\n</div>\n\n<div class=\"container-fluid mt-3 mb-4 p-0\" >\n    <div class=\"card card-body\">\n    <div class=\"row  mt-4 mb-4\">\n        <div class=\"col-sm-9\">\n            <h4 style=\"color: crimson;\">3) Vimal Dushing</h4>\n            <p class=\"lead\" style=\"text-align: justify;\">\n                Vimal was a young widow with two children – two daughters and a son. Being a member of the Self Help\n                group, she was able to educate her three children to become graduates and get good jobs. Marriage in\n                this area is a great financial drain on the people, especially widows. She has managed to arrange for\n                the marriage of all three children by taking on a small job in a school and small sewing jobs in the\n                village.\n            </p>\n\n            <p class=\"lead\" style=\"text-align: justify;\">\n                It is her determination and honesty that have stood by her to reach this moment of self reliance.\n            </p>\n        </div>\n        <div class=\"col-sm-3\">\n            <img class=\"img-fluid\" src=\"assets/images/success-stories3.jpg\" alt=\"\" style=\"height:350px;width: 100%;\">\n        </div>\n    </div>\n</div>\n</div>\n");

